import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { PaymentDetailsListComponent } from './payment-details-list.component';
import { LoderModule } from 'src/app/common/loder/loder.module';
import { PaginationModule } from 'src/app/common/pagination/pagination.module';
import { PaymentDetailsService } from 'src/app/service/payment-details.service';

const routes: Routes = [
  {
    path: '',
    component: PaymentDetailsListComponent,
    // data: { title: 'company_list' }
  },
  {
    path: 'add',
    loadChildren: () => import('../payment-details-add/payment-details-add.module').then(m => m.PaymentDetailsAddModule),
    // data: { title: 'add_company' }
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('../payment-details-add/payment-details-add.module').then(m => m.PaymentDetailsAddModule),
    // data: { title: 'edit_company' }
  },
];

@NgModule({
  declarations: [
    PaymentDetailsListComponent
  ],
  imports: [
    CommonModule,
    LoderModule,
    PaginationModule,
    RouterModule.forChild(routes)
  ],
  providers: [
  PaymentDetailsService
  ]
})
export class PaymentDetailsListModule { }
