import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';


@Injectable()
export class PaymentDetailsService {

  API_URL: string = environment.APIEndpoint;

  constructor(
    private http: HttpClient,
  ) { }

  getPaymentDetailsList(param): Observable<any> {
    return this.http.get(`${this.API_URL}/payment-details`, { params: param });
  }

  addPaymentDetails(data): Observable<any> {
    return this.http.post(`${this.API_URL}/payment-details`, data);
  }

  editPaymentDetails(id, data): Observable<any> {
    return this.http.put(`${this.API_URL}/payment-details/${id}`, data);
  }

  deletePaymentDetails(id): Observable<any> {
    return this.http.delete(`${this.API_URL}/payment-details/${id}`);
  }

  getPaymentDetailsById(id, param = null): Observable<any> {
    return this.http.get(`${this.API_URL}/payment-details/${id}`, { params: param });
  }




}

